// import Type from './Type.vue';

export default {
  name: 'Field',
  components: {
    // Type,
  },
  props: {
    field: Object,
  },
  methods: {
    isComplexType() {
      return ['array', 'enum', 'object'].includes(this.field.type);
    },
    getRequired() {
      return 'required' in this.field ? this.field.required : true;
    },
  },
};
