<template>
  <div class="menu"
       :style="{width: width+'px'}">
    <div class="title"
         @click="handleClickName">{{baseInfo.project_name}}</div>
    <div class="docs__menu"
         v-loading="loading">
      <div class="doc__item"
           v-for="doc in baseInfo.api_docs"
           :key="doc.filename">
        <div class="doc__name"
             @click="showApi(doc.filename)">{{doc.name}}</div>
        <div class="doc__list"
             v-if="currentFilename === doc.filename">
          <div class="line"></div>
          <div class="line--current"
               :style="{top: currentApiIndex * 40 + 'px'}"></div>
          <div class="doc"
               v-for="(api,index) in currentDoc.apis"
               @click="getApi(api,index)"
               :class="{'doc--active': currentApi === api.name}"
               :key="api.name">
            {{api.name}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: Number,
      default: 250,
    },
  },
  computed: {
    baseInfo() {
      return this.$store.state.baseInfo;
    },
  },
  data() {
    return {
      loading: false,
      currentFilename: '',
      currentDoc: {},
      currentApi: '',
      currentApiIndex: -1,
    };
  },
  mounted() {
    const isMain = localStorage.getItem('app.isMain');
    if (isMain === 'false') {
      const currentFilename = localStorage.getItem('lastFileName');
      if (currentFilename) {
        this.showApi(currentFilename);
      }
    }
  },
  methods: {
    handleClickName() {
      this.$emit('clickName');
      this.currentApi = '';
      this.currentApi = -1;
      this.currentDoc = {};
      this.currentFilename = '';
    },
    showApi(filename) {
      const lastFilename = localStorage.getItem('lastFileName');
      this.loading = true;
      this.$store.dispatch('getDocs', { filename })
        .then((doc) => {
          this.currentDoc = doc;
          this.currentFilename = filename;
          this.$emit('selectDoc', doc);
          localStorage.setItem('lastFileName', filename);
          localStorage.setItem('doc', JSON.stringify(doc));
          if (doc.apis.length > 0) {
            const lastApiIndex = Number(localStorage.getItem('currentApiIndex')) || 0;
            const defaultIndex = lastFilename === filename ? lastApiIndex : 0;
            this.getApi(doc.apis[defaultIndex], defaultIndex);
          }
        }).finally(() => {
          this.loading = false;
        });
    },
    getApi(api, index) {
      this.currentApi = api.name;
      this.currentApiIndex = index;
      this.$emit('selectApi', api);
      localStorage.setItem('currentApiIndex', index);
      localStorage.setItem('api', JSON.stringify(api));
    },
  },
};
</script>
<style lang="scss" scoped>
.menu {
  position: fixed;
  box-sizing: border-box;
  height: 100vh;
  box-shadow: 2px 0 5px #ddd;
  cursor: pointer;

  .title {
    padding: 20px 10px;
    font-size: 1.2rem;
    font-weight: 600;
  }

  .doc__list {
    position: relative;

    .line {
      position: absolute;
      height: 100%;
      width: 2px;
      left: 10px;
      background-color: #ddd;

      &--current {
        position: absolute;
        height: 40px;
        width: 2px;
        left: 10px;
        background-color: #2372fa;
        transition: 0.2s;
      }
    }
  }

  .doc__name,
  .doc {
    padding: 10px;
    transition: 0.2s;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    &:hover,
    &--active {
      color: #2372fa;
      background-color: rgba(35, 114, 250, 0.1);
    }
  }

  .doc {
    padding: 10px 0 10px 30px;
    font-size: 0.9rem;
  }
}
</style>
