<template>
  <div class="doc__container">
    <div class="name">{{doc.name}}</div>
    <div class="desc"
         v-html="docDesc">
    </div>
  </div>
</template>

<script>
import { markdown } from '../../utils';

export default {
  props: {
    doc: Object,
  },
  computed: {
    docDesc() {
      return this.doc.desc ? markdown(this.doc.desc) : '';
    },
  },
};
</script>

