import axios from 'axios';
import docs from './docs.json';
import post from './post.json';
import auth from './auth.json';

const map = {
  'api_docs/post.json': post,
  'api_docs/auth.json': auth,
  'api_docs/药剂师/post.json': post,
};

const isDev = false; // process.env.NODE_ENV === 'development';

export const getApiDocs = () => (isDev ? Promise.resolve({ data: docs }) : axios.get('http://139.155.127.14:9000/__api_docs/'));

export const getDocs = filename => (isDev ? Promise.resolve({ data: map[filename] }) : axios.get(`http://139.155.127.14:9000/__api_docs/api_data/?filename=${filename}`));
