<template>
  <div class="app">
    <Menu :width="menuWidth"
          @clickName="handleSelectName"
          @selectDoc="handleSelectDoc"
          @selectApi="handleSelectApi" />
    <div :style="{marginLeft: menuWidth + 'px'}"
         class="api-container">
      <Main v-if="isMain && baseInfo.read_me"
            :desc="baseInfo.read_me" />
      <template v-else>
        <Doc :doc="doc" />
        <Api :api="api" />
      </template>
    </div>
  </div>
</template>

<script>
import Main from './components/Main/index.vue';
import Menu from './components/Menu/index.vue';
import Api from './components/API/index.vue';
import Doc from './components/Doc/index.vue';

export default {
  components: {
    Main,
    Menu,
    Api,
    Doc,
  },
  mounted() {
    this.$store.dispatch('getApiDocs');
    const isMain = localStorage.getItem('app.isMain');
    this.isMain = isMain === 'true' || isMain === null;
  },
  computed: {
    baseInfo() {
      return this.$store.state.baseInfo;
    },
  },
  data() {
    return {
      api: {},
      doc: {},
      isMain: true,
      menuWidth: 250,
    };
  },
  methods: {
    handleSelectName() {
      this.isMain = true;
      localStorage.setItem('app.isMain', true);
    },
    handleSelectDoc(doc) {
      this.doc = doc;
      this.isMain = false;
      localStorage.setItem('app.isMain', false);
    },
    handleSelectApi(api) {
      this.api = api;
      this.isMain = false;
      localStorage.setItem('app.isMain', false);
    },
  },
};
</script>

<style>
html,
body,
.app,
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
* {
  color: inherit #202d40;
}
</style>
<style lang="scss" scoped>
.app {
  display: flex;

  .api-container {
    flex: 1;
    padding: 20px;
  }
}
</style>
