<template>
  <div class="container">
    <div class="name">{{name}}</div>
    <el-button type="primary"
               @click="testApi">
      测试
    </el-button>
    <div class="request"
         v-if="data.request">
      <div class="body">请求Body</div>
      <div ref="copy"
           class="copy"
           @click="copy"
           :data-clipboard-text="request"
           data-clipboard-action="copy">
        复制
      </div>
      <pre class="highlight"
           v-html="data.request"
           v-highlight>
    </pre>
    </div>
    <div class="none"
         v-else>
      无请求Body
    </div>
    <div class="response"
         v-loading="loading">
      <div class="body">响应Body</div>
      <div ref="copy"
           class="copy"
           @click="copy"
           :data-clipboard-text="response"
           data-clipboard-action="copy">
        复制
      </div>
      <pre class="highlight"
           v-html="data.response"
           v-highlight>
    </pre>
    </div>
  </div>
</template>

<script>
import { Message, Button } from 'element-ui';
import Clipboard from 'clipboard';
import createAPI from '../../api';

export default {
  components: {
    'el-button': Button,
  },
  props: {
    api: Object,
    data: Object,
    name: String,
  },
  data() {
    return {
      response: '',
      loading: false,
    };
  },
  computed: {
    request() {
      return JSON.stringify(this.data.request);
    },
  },
  watch: {
    data(d) {
      if (d) {
        this.response = JSON.stringify(d.response);
      }
    },
  },
  methods: {
    copy() {
      const clipboard = new Clipboard('.copy');
      clipboard.on('success', () => {
        Message.success('复制成功');
        clipboard.destroy();
      });
    },
    getFormData() {
      const formData = new FormData();
      Object.keys(this.data.request).forEach((key) => {
        const value = this.data.request[key];
        const ext = key.split('.').pop();
        // 图片文件
        if (/(jp?eg|png)$/.exec(ext)) {
          const buffer = new ArrayBuffer(32);
          const file = new File(buffer, value, { type: `image/${ext}` });
          formData.append(key, file);
        } else {
          formData.append(key, value);
        }
      });
      return formData;
    },
    testApi() {
      this.loading = true;
      const isFormData = this.api.body_mode === 'formdata';
      const headers = {
        'Content-Type': isFormData ? 'multipart/form-data' : `application/${this.api.body_mode}`,
      };
      createAPI({
        baseURL: '/',
        headers,
        url: this.api.url,
        method: this.api.method,
        body: isFormData ? this.getFormData() : this.data.request,
      }).then((res) => {
        this.response = res.data;
      }).catch((err) => {
        Message.error(err.message);
      }).finally(() => {
        this.loading = false;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;

  .request,
  .response {
    position: relative;
  }

  .body,
  .copy {
    position: absolute;
    color: #ccc;
    top: 5px;
    font-size: 0.8rem;
    cursor: pointer;
  }

  .body {
    left: 10px;
  }

  .copy {
    right: 10px;
  }

  .highlight {
    padding-top: 25px;
    border-radius: 5px;
  }

  .none {
    margin: 10px 0;
  }
}
</style>
