import Vue from 'vue';
import Vuex from 'vuex';
import { getApiDocs, getDocs } from '../api/docs';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    baseInfo: {},
    docs: {},
  },
  mutations: {
    SET_BASE_INFO(state, info) {
      state.baseInfo = info;
    },
    ADD_DOCS(state, { filename, doc }) {
      state.docs[filename] = doc;
    },
  },
  actions: {
    getApiDocs({ commit }) {
      getApiDocs().then((res) => {
        commit('SET_BASE_INFO', {
          ...res.data,
          api_docs: res.data.api_docs.sort((a, b) => a.order - b.order),
        });
      });
    },
    getDocs({ state, commit }, { filename }) {
      return getDocs(filename).then((res) => {
        if (!(filename in state.docs)) {
          commit('ADD_DOCS', { filename, doc: res.data });
        }
        return state.docs[filename];
      });
    },
  },
});
