<template>
  <div class="type">
    <div class="title">
      {{title}}
    </div>
    <div class="body"
         v-if="innerBody.length > 0">
      <div class="field"
           v-for="field in innerBody"
           :key="field.prop">
        <Field :field="field" />
      </div>
    </div>
    <div class="none"
         v-else>无参数</div>
  </div>
</template>

<script>
import Field from './Field.vue';

export default {
  name: 'Type',
  props: {
    title: String,
    body: Object,
  },
  computed: {
    innerBody() {
      const body = [];
      if (this.body) {
        Object.keys(this.body).forEach((prop) => {
          body.push({ prop, ...this.body[prop] });
        });
      }
      return body;
    },
  },
  components: {
    Field,
  },
};
</script>

<style lang="scss" scoped>
.type {
  .title {
    padding: 10px 0;
    font-weight: 600;
    font-size: 1.2rem;
    border-bottom: 2px solid #ddd;
  }

  .field {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }

  .none {
    margin: 10px 0;
  }
}
</style>
